
import './App.css';


import React from 'react';
import Marquee from './components/Marquee/Marquee';





import { useState } from 'react';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Back from './components/BackDrop/BackDrop';

import MyCanvas from './components/Canvas/MyCanvas';
import Header from './components/Header/Header';
import AboutProject from './components/AboutProject/AboutProject';
import Contact from './components/Contact/Contact';


const App = () => {

  let menuBar = () => {

    (sideToggle === false ? setSideToggle(true) : setSideToggle(false))
  }

  const [sideToggle, setSideToggle] = useState(false)


  const about = React.createRef()
  const project = React.createRef()
  const contacts = React.createRef()

  return (
    <div className="App">


      <SideDrawer menubar={menuBar} sideToggle={sideToggle} about={about} project={project} contacts={contacts} />
      <Back menubar={menuBar} sideToggle={sideToggle} />

      <div className='canvasContainer'>
        
        <Header menubar={menuBar} />
        <MyCanvas />

      </div>

      <div className='contentContainer'>

        <div>

          <div className='headerMarquee'>
            <Marquee text='ДОБРО ПОЖАЛОВАТЬ ' text2='НА СТРАНИЦУ ' span='spanHeader' />
          </div>

          <AboutProject />

          <Contact/>

        </div>
      </div>



    </div>



  );
}

export default App;
