import './CarouselItem.css'

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import travelanywhere from '../../img/1.png'
import planner from '../../img/2.png'
import button from '../../img/3.png'
import sneakers from '../../img/4.png'
import Slider from "react-slick";

export default function SimpleSlider() {
    var settings = {

        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        centerPadding: '40px',
    }

    return (
        <Slider {...settings}>
            <div className='carouselItemContainer'>
                <div   className='carouselItem'>
                    <a href='https://www.travelanywherekey.com/' target='_blant'>
                        <img className='projectPhoto' src={travelanywhere} />

                    </a>
                   
                </div>


            </div>
            <div className='carouselItemContainer'>
                <div   className='carouselItem'>
                    <a href='https://mariarass.github.io/planner/' target='_blant'>
                        <img className='projectPhoto' src={planner} />

                    </a>
                </div>

            


            </div>
            <div className='carouselItemContainer'>
                <div   className='carouselItem'>
                    <a href='https://mariarass.github.io/sneakers/' target='_blant'>
                        <img className='projectPhoto' src={sneakers} />

                    </a>
                </div>


            </div>
            <div className='carouselItemContainer'>
                <div   className='carouselItem'>
                    <a href='https://mariarass.github.io/button/' target='_blant'>
                        <img className='projectPhoto' src={button} />

                    </a>
                </div>


            </div>
          
           
        </Slider>
    );
}