import { ReactComponent as Language } from '../../img/language.svg'
import { ReactComponent as Menu } from '../../img/menu.svg'

const Header = (props) => {

 
    return(



    <div className='languageContainer'>
        <div className='language'>
          
            <Menu className='menuSvg' onClick={() => { props.menubar() }} />
        </div>
    </div>)

}

export default Header