
import { Canvas } from '@react-three/fiber'
import Girl from './../../Girl'

import { Suspense } from 'react';
const MyCanvas = (props) => {
    return <Canvas className='canvas' camera={{ position: [0, 0, 150] }}>

        <ambientLight intensity={0} />
        <directionalLight position={[-5, 5, 2]} color="pink" />
        <directionalLight position={[-70, 5, 50]} color="pink" />
        <directionalLight position={[0, -40, 0]} color="pink" />
        <directionalLight position={[10, 0, 0]} color="white" />
        <directionalLight position={[0, 0, -10]} color="pink" />
        <Suspense fallback={null}>
            <Girl />
        </Suspense>

    </Canvas>
}

export default MyCanvas