
import './SideDrawer.css'
const SideDrawer = (props) => {


    const sideDrawerStyle = ['sideDrawer']
    if (props.sideToggle) {
        sideDrawerStyle.push('show')
    }

    let scrollToMyRef = (element) => {window.scrollTo(0, element)
    props.menubar()}

    return (

        <div className={sideDrawerStyle.join(' ')}>
            <div className='links'>
            <div  onClick={()=>{scrollToMyRef(300)}} className='link'>
             ОБО МНЕ
            </div >
            <div onClick={()=>{scrollToMyRef(1000)}} className='link'>
             МОИ ПРОЕКТЫ
            </div>
            <div  onClick={()=>{scrollToMyRef(1600)}}  className='link'>
             МОИ КОНТАКТЫ
            </div>
           
            </div>

        </div>)
}
export default SideDrawer