import { ReactComponent as MyPhoto } from '../../img/myphoto.svg'
import { ReactComponent as Mambl1 } from '../../img/mambl.svg'
import { ReactComponent as Mambl2 } from '../../img/mambl.svg'
import { ReactComponent as Mambl3 } from '../../img/mambl.svg'
import SimpleSlider from '../../components/Carousel/CarouselItem';
import Marquee from '../../components/Marquee/Marquee';


const AboutProject = (props) => {
    return (
        <div className='content'>

            <div className='About' >
                <div className='photoContainer'>

                    <div className='photo'>
                    </div>
                    <MyPhoto className='myPhoto' />

                </div>

                <div className='textAbout'>

                    <p className='text'>Добрый день!<br/><br/>
                        В последние годы я проходила обучение по специальности «Программирование в компьютерных системах », <br/>
                        На данный момент ищу работу на позицию «Junior React Developer” удаленно   <br/>
                        Для этой позиции имею знания<br/>-js<br/> -react,<br/> -redux,<br/> -html,-css,<br/>MySQL,MongoDB </p>
                    

                </div>

            </div>



            <div className='aboutContainer'>
            </div>

            <div className='projectContainer' >
                <Mambl1 className='mambl1' />
                <Mambl2 className='mambl2' />
                <Mambl3 className='mambl3' />
            </div>

            <div className='project' >

                <div className='Marquee'>
                    <Marquee text='МОИ ПРОЕКТЫ ' text2='МОИ ПРОЕКТЫ ' span='spanProject' />
                </div>

                <div className='projectItems'>
                    <div className='projectItem'>
                        <SimpleSlider />
                    </div>
                </div>

            </div>



        </div>

    )
}

export default AboutProject